// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);


// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-eclipse:                     #3d3333;
$color-taupe-grey:                  #888387;
$color-old-lavender:                #7a787a;
$color-black-80:                    rgba(0, 0, 0, 0.8);
$color-gainsboro-20:                rgba(217, 217, 217, 0.2);
$color-almond:                      #f2d7d1;
$color-dim-gray:                    #767272;
$very-light-grey:                   #cccccc;

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default:                        30px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-space-grotesk:                    "Space Grotesk", "Arial", sans-serif;
$ff-nunito:                           "Nunito", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;
$trans-500:                         0.5s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1512:                           1512px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
